let api = {
  authCode: { url: 'api/v1/vcode', method: 'get' }, //
  register: { url: 'api/v1/register' }, //
  login: { url: 'api/v1/login' }, //
  forget: { url: 'api/v1/forget_password' }, //
  index: { url: 'api/v1/preset', method: 'get' }, //
  goods: { url: 'api/v1/goods', method: 'get' }, //
  series: { url: 'api/v1/series', method: 'get' }, //
  goodsDetail: { url: 'api/v1/goods/detail/:id', method: 'get' }, //
  cart: { url: 'api/v1/cart', method: 'get' }, //
  collect: { url: 'api/v1/collects' }, //

  getUserInfo: { url: 'api/v1/user_info', method: 'get' },  //
  editUserInfo: { url: 'api/v1/user_info', method: 'post' },  //
  getAddress: { url: 'api/v1/address', method: 'get'}, //
  addAdress: { url: 'api/v1/address', method: 'post'}, //
  setAdress: { url: 'api/v1/address', method: 'put'}, //
  delAdress: { url: 'api/v1/address', method: 'delete'}, //
  getProvince: { url: '/api/v1/province', method: 'get'}, //
  getCity: { url: '/api/v1/city', method: 'get'}, //
  getDistrict: { url: '/api/v1/district', method: 'get'} //
};

for (var k in api) {
  let urlHost = window.htp.apihost;
  let url = api[k].url;

  if (process.env.NODE_ENV === 'development') {
    urlHost = '/proxy/';
  }
  api[k].url = urlHost + url;
}

export default api;



